import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/Index'
import Page2 from '../page/Page2'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/page2',
      name: 'page2',
      component: Page2
    }
  ]
})



