<template>
  <div class="page-content">
    <div class="page-header">
      <h1>
        流程字段列表
      </h1>
    </div>

    <div class="page-header pos-rel">
      <h1>
        <div id="nav-search">
          <span class="input-icon">
            <input type="text" placeholder="Search ..." class="nav-search-input" id="nav-search-input"
                   autocomplete="off">
            <i class="ace-icon fa fa-search nav-search-icon"></i>
          </span>
          <button class="btn btn-xs btn-primary">确定</button>
          <button class="btn btn-xs btn-success" data-toggle="modal" data-target="#myModal">添加流程字段
          </button>
          <button class="btn btn-xs btn-primary" @click="jump">跳页实验</button>
        </div>
      </h1>
    </div>

    <div class="row">
      <div class="col-xs-12">
        <!-- PAGE CONTENT BEGINS -->
        <div class="row">
          <div class="col-lg-12">
            <table class="table table-striped table-bordered">
              <thead>
              <tr>
                <th class="center">#</th>
                <th>名称</th>
                <th class="hidden-xs">类型</th>
                <th class="hidden-480">值</th>
                <th>操作</th>
              </tr>
              </thead>

              <tbody>
              <tr>
                <td class="center">1</td>
                <td>申请人/Applicant</td>
                <td>input</td>
                <td>必填项</td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>

              <tr>
                <td class="center">2</td>
                <td>申请日期/Application Date</td>
                <td>date</td>
                <td> --- </td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>

              <tr>
                <td class="center">3</td>
                <td>申请原因/Apply Reason</td>
                <td>textarea</td>
                <td>必填项</td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>

              <tr>
                <td class="center">4</td>
                <td>付款方式/Payment Method</td>
                <td>select</td>
                <td>电汇 Wire Transfer,转账 Transfer,现金 Cash</td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>
              <tr>
                <td class="center">5</td>
                <td>福利类型/Benefit Type</td>
                <td>checkbox</td>
                <td>结婚礼金,生育礼金</td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>

              <tr>
                <td class="center">6</td>
                <td>性别/Male</td>
                <td>radio</td>
                <td>男 Man,女 Woman</td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>

              <tr>
                <td class="center">7</td>
                <td>附件/Accessory</td>
                <td>inputFile</td>
                <td> --- </td>
                <td>
                  <i class="ace-icon fa fa-pencil align-top bigger-125 text-info cursor"></i>&nbsp;&nbsp;
                  <i class="ace-icon fa fa-trash-o align-top bigger-125 text-danger cursor"></i>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div><!-- /.row -->
        <!-- PAGE CONTENT ENDS -->
      </div><!-- /.col -->
    </div><!-- /.row -->

    <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">
              添加流程字段
              <button class="btn btn-xs btn-success add-btn" style="margin-left: 10%;">添加流程字段</button>
            </h4>
          </div>
          <div class="modal-body">
            <!--<form>-->
            <div class="section">
              <div class="pos-rel">
                <div class="form-group clearfix">
                  <input type="text" placeholder="请输入字段名称" class="fieldName inline middle">
                  <select class="fieldType inline middle" style="height: 34px;">
                    <option value="0" selected="">请选择字段类型</option>
                    <option value="1">input</option>
                    <option value="2">select</option>
                    <option value="3">checkbox</option>
                    <option value="4">radio</option>
                    <option value="5">textarea</option>
                    <option value="6">inputFile</option>
                    <option value="7">date</option>
                  </select>
                  <input type="text" placeholder="请输入字段Value,多个值以,分割" class="fieldValue middle width-30 hide">
                  <div class="fieldRule1 hide middle">
                    <label>
                      <input name="form-field-checkbox" type="checkbox" class="ace fieldIsEmpty">
                      <span class="lbl"> 是否为必填项</span>
                    </label>
                  </div>
                  <div class="fieldRule2 hide middle">
                    <label>
                      <input name="form-field-checkbox" type="checkbox" class="ace fieldIsNum">
                      <span class="lbl"> 是否为数字</span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div class="tips text-danger"></div>
            <!--</form>-->
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
            <button type="button" class="btn btn-primary" id="btn-submit">提交更改</button>
          </div>
        </div><!-- /.modal-content -->
      </div><!-- /.modal -->
    </div>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: '首页'
    }
  },
  methods: {
    jump: function () {
      this.$router.push('/page2')
    }
  }
}
</script>
<style scoped>
</style>



